<template>
  <view class="container">
    <view class="register">
      <view class="info_box">
        <view class="info_item">
          <text class="label">跟进方式</text>
          <view class="flexBet">
            <ld-select
              :underlist="followUpWayList"
              list-key="label"
              value-key="value"
              placeholder="跟进方式"
              v-model="form.behavior"
              @change="selectFollow"
            ></ld-select>
            <uni-icons type="arrowright" size="12" color="#999"></uni-icons>
          </view>
        </view>
        <view class="info_item">
          <text class="label">意向级别</text>
          <view class="flexBet">
            <ld-select
              :underlist="intentionLevels"
              list-key="label"
              value-key="value"
              placeholder="请选择意向级别"
              v-model="form.intentionLevel"
              @change="selectIntenLeve"
            ></ld-select>
            <uni-icons type="arrowright" size="12" color="#999"></uni-icons>
          </view>
        </view>
        <remarkBox :textName="'跟进内容'" @getValue="_getfollowValue"></remarkBox>
        <view class="info_item">
          <text class="label">下次跟进</text>
          <dateTime @getDate="_getDate"></dateTime>
        </view>
      </view>
    </view>
    <view class="footer" @click="saveTap">保存</view>
  </view>
</template>

<script>
import httpUtils from "@/utils/httpUtils.js";
import Tools from "@/utils/tools.js";
import dateTime from "@/components/dateTime.vue";
import remarkBox from "@/components/remark.vue";
export default {
  data() {
    return {
      form: {
        id: "", // 客户id
        behavior: "", // 跟进方式
        content: "", // 跟进内容
        intentionLevel: "", // 意向级别
        followProjectedTime: "", // 下次跟进时间
      },
      followUpWayList: [], // 跟进方式
      followIndex: "", // 跟进方式索引
      intentionLevels: [], // 意向级别
      intensionIndex: "", // 意向级别索引
      cusId: "", // 客户id
    };
  },
  onLoad(options) {
    this.cusId = options.id;
    this._getList();
  },
  methods: {
    // 获取下拉框列表
    _getList() {
      let that = this;
      Tools.showLoading("加载中");
      httpUtils.sendHttpRequest(
        "2024",
        Object.assign({}, { id: that.cusId }),
        function (res) {
          Tools.hideLoading();
          if (res.data.code == "0") {
            that.followUpWayList = res.data.data.followUpWayList;
            that.intentionLevels = res.data.data.intentionLevelList;
          }
        }
      );
    },
    // selectFollow(e, val) {
    // 	this.followIndex = e.target.value
    // 	this.form.behavior = val[this.followIndex].value
    // },
    // 跟进方式选择
    selectFollow(val) {
      this.form.behavior = val;
    },
    // 意向级别选择
    selectIntenLeve(val) {
      this.form.intentionLevel = val;
    },
    // 跟进内容回调
    _getfollowValue(data) {
      that.form.content = data;
    },
    // 日期回调
    _getDate(data) {
      this.form.followProjectedTime = data;
    },
    // 确定
    saveTap() {
      let that = this;
      that.form.id = that.cusId;
      let parms = Object.assign({}, that.form);

      Tools.showLoading("保存中");
      httpUtils.sendHttpRequest("2025", that.form, function (res) {
        Tools.hideLoading();
        if (res.data.code == "0") {
          Tools.showSuccessToast("保存成功");
          setTimeout(function () {
            uni.reLaunch({
              url: "/pages/salemanVisual/customer/index",
            });
          }, 500);
        }
      });
    },
  },
  components: {
    dateTime,
    remarkBox,
  },
};
</script>

<style lang="scss">
@import "@/styles/form.scss";
page {
  background-color: #f6f6f6;
}
</style>
